<template>
<div>
  <!--    面包屑导航-->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>商品管理</el-breadcrumb-item>
    <el-breadcrumb-item>商品列表</el-breadcrumb-item>
  </el-breadcrumb>
  <el-card>
    <el-alert
        title="注意：只允许为第三级分类设置相关参数!"
        type="warning"
        show-icon>
    </el-alert>
<!--    选择商品分类区域-->
    <el-row>
      <el-col>
        <span>选择商品分类:</span>
<!--        选择商品分类级联选择框-->
        <el-cascader
            expand-trigger="hover"
            v-model="selectedCateKeys"
            :options="cateList"
            :props="cateProps"
            @change="handleChange"
            clearable
            change-on-select
        >
        </el-cascader>
      </el-col>
    </el-row>
<!--    tab页签区域-->
    <el-tabs v-model="activeName" @tab-click="handleTabClick">
      <el-tab-pane label="动态参数" name="many">
        <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">添加参数</el-button>
<!--        动态参数表格-->
        <el-table :data="manyTableData" border stripe>
<!--          展开行-->
          <el-table-column type="expand">
            <template slot-scope="scope">
<!--              循环渲染tag标签-->
              <el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleClose(index,scope.row)">
                {{item}}
              </el-tag>
<!--              输入文本框-->
              <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
              >
              </el-input>
<!--              添加按钮-->
              <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
            </template>
          </el-table-column>
<!--          索引列-->
          <el-table-column type="index"></el-table-column>
          <el-table-column label="参数名称" prop="attr_name"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)">编辑</el-button>
              <el-button type="danger" icon="el-icon-edit" size="mini" @click="removeParamById(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="静态属性" name="only">
        <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible=true">添加参数</el-button>
        <!--        静态参数表格-->
        <el-table :data="onlyTableData" border stripe>
          <!--          展开行-->
          <el-table-column type="expand">
            <template slot-scope="scope">
              <!--              循环渲染tag标签-->
              <el-tag v-for="(item,index) in scope.row.attr_vals" :key="index" closable @close="handleClose(index,scope.row)">
                {{item}}
              </el-tag>
              <!--              输入文本框-->
              <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
              >
              </el-input>
              <!--              添加按钮-->
              <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
            </template>
          </el-table-column>
          <!--          索引列-->
          <el-table-column type="index"></el-table-column>
          <el-table-column label="属性名称" prop="attr_name"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)">编辑</el-button>
              <el-button type="danger" icon="el-icon-edit" size="mini" @click="removeParamById(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </el-card>
  <el-dialog
      :title="'添加'+titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
  >
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
      <el-form-item :label="titleText" prop="attr_name">
        <el-input v-model="addForm.attr_name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addParams">确 定</el-button>
  </span>
  </el-dialog>
  <el-dialog
      :title="'编辑'+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
  >
    <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
      <el-form-item :label="titleText" prop="attr_name">
        <el-input v-model="editForm.attr_name"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editParamInfo">确 定</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: "Params",
  data(){
    return{
      cateList:[],
      //级联选择框配置对象
      cateProps:{
        value:'cat_id',
        label:'cat_name',
        children:'children'
      },
      //级联选择框双向绑定
      selectedCateKeys:[],
      // 被激活页签名称
      activeName:'many',
      manyTableData:[],
      onlyTableData:[],

      addDialogVisible:false,

      addForm:{
        attr_name:''
      },
      addFormRules:{
        attr_name:[
          {required: true, message: '请输入参数名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ]
      },
      editForm:{
        attr_name:''
      },
      editFormRules:{
        attr_name:[
          {required: true, message: '请输入参数名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ]
      },
      editDialogVisible:false,

    }
  },
  created() {
    this.getCateList()
  },
  methods:{
    async getCateList() {
      const {data:res}=await this.$http.get('categories')
      if (res.meta.status!==200){
        return this.$message.error('获取商品数据失败!')
      }
      this.cateList=res.data
    },
    async getParamsData(){
      if (this.selectedCateKeys.length!==3){
        this.selectedCateKeys=[]
        this.manyTableData=[]
        this.onlyTableData=[]
        return
      }
      console.log(this.selectedCateKeys)
      //  更具所选分离id，和当前所处的面板获取对应的参数
      const {data:res}=await this.$http.get(`categories/${this.cateId}/attributes`,{
        params:{
          sel:this.activeName
        }
      })
      if (res.meta.status!==200){
        return this.$message.error('获取参数列表失败')
      }
      res.data.forEach(item=>{
        item.attr_vals=item.attr_vals ? item.attr_vals.split(' ') : []
        // 控制文本框的显示与隐藏
        item.inputVisible=false
        // 文本框输入的值
        item.inputValue=''
      })
      console.log(res.data)
      if (this.activeName==='many'){
        this.manyTableData=res.data
      }else{
        this.onlyTableData=res.data
      }
    },
    //级联选择框选中项变化触发
    handleChange(){
      this.getParamsData()
    },
    // tab页签点击事件的处理函数
    handleTabClick(){
      this.getParamsData()
    },
    addDialogClosed(){
      this.$refs.addFormRef.resetFields()
    },
    //添加编辑动态参数or静态属性
    addParams(){
      this.$refs.addFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.post(`categories/${this.cateId}/attributes`,{
          attr_name:this.addForm.attr_name,
          attr_sel:this.activeName,
        })
        if (res.meta.status!==201){
          return this.$message.error('添加参数失败')
        }
        this.$message.success('添加参数成功')
        this.addDialogVisible=false
        this.getParamsData()
      })
    },
    // 展示编辑动态参数or静态属性
    async showEditDialog(id){
      const {data:res}=await this.$http.get(`categories/${id.cat_id}/attributes/${id.attr_id}`,{
        attr_sel:this.activeName
      });
      this.editForm=res.data
      this.editDialogVisible=true
    },
    //关闭重置
    editDialogClosed(){
      this.$refs.editFormRef.resetFields()
    },
    // 修改编辑动态参数or静态属性
    async editParamInfo(){
      this.$refs.editFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.put(`categories/${this.editForm.cat_id}/attributes/${this.editForm.attr_id}`,{
          attr_name:this.editForm.attr_name,
          attr_sel:this.activeName
        })
        if (res.meta.status!==200){
          return this.$message.error('编辑提交参数失败')
        }
        //关闭对话框
        this.editDialogVisible=false
        //刷新参数列表
        this.getParamsData();
        //提示更新角色信息成功
        this.$message.success('编辑提交参数成功')
      })
    },
    async removeParamById(id){
      const res=await this.$confirm(`此操作将永久删除该${this.titleText}, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err=>err)
      if (res!=='confirm'){
        return this.$message.info('已取消删除')
      }
      const {data:response}=await this.$http.delete(`categories/${id.cat_id}/attributes/${id.attr_id}`)
      if (response.meta.status!==200){
        return this.$message.error('分类删除失败')
      }
      this.$message.success('该分类已删除')
      this.getParamsData();
    },
    //文本框失去焦点或按enter都会触发
    async handleInputConfirm(row){
      if (row.inputValue.trim().length===0){
        row.inputValue=''
        row.inputVisible=false
        return
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue=''
      row.inputVisible=false
      // 发起网络请求，保存数据
      this.saveAttrValues(row)
    },
    //点击按钮展示文本输入框
    showInput(row){
      row.inputVisible=true
      //文本框自动获得焦点
      // $nextTick 当页码上元素被重新渲染之后，才会指定回调函数中的代码
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    //删除对应的参数和选项
    handleClose(index,row){
      row.attr_vals.splice(index,1);
      this.saveAttrValues(row)
    },
    async saveAttrValues(row){
      const{data:res}=await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name:row.attr_name,
        attr_sel:row.attr_sel,
        attr_vals:row.attr_vals.join(' ')
      })
      if(res.meta.status!==200){
        return this.$message.error('修改参数项失败')
      }
      this.$message.success('修改参数项成功')
    }
  },
  computed:{
    isBtnDisabled(){
      if (this.selectedCateKeys.length!==3){
        return true
      }
      return false
    },
    cateId(){
      if (this.selectedCateKeys.length==3)
        return this.selectedCateKeys[2]
      return null
    },
    // 动态计算标题文本
    titleText(){
      if (this.activeName==='many'){
        return '动态参数'
      }
      return '静态属性'
    }
  }
}
</script>

<style lang="less" scoped>
  .el-row{
    margin: 15px 0;
  }
  .el-tag{
    margin: 0 10px;
  }
  .el-input{
    width: 120px;
  }
</style>